.image-upload-link {
    background-color: $water;
    @include rem('border', 1px dashed $link_color);
    @include border-radius();

    &__start-link {
        display: block;
        text-align: center;
        @include rem('padding', 14px);
    }

    &__change-link {
        display: block;
        text-decoration: none;
        @include rem('padding', 12px 12px 0);
        @include transition(opacity, $duration: 100ms);

        &:hover,
        &:active {
            opacity: .84;
            text-decoration: none;
        }

        img {
            width: 100%;
            display: block;
            background-color: $spindle;
            @include rem('min-height', 60px);
        }

        .figcaption {
            display: block;
            color: $text_color;
            @include rem('padding', 6px 0 5px);
        }
    }

    &__delete-link {
        display: block;
        text-align: center;
        @include rem('border-top', 1px solid $silver);
        @include rem('margin', 0 14px);
        @include rem('padding', 6px 0);
    }
}
