// Article
// -------------------------------------------------------------

.input--title {
    @include rem('font-size', 19px);
    line-height: ( 28px / 19px );
    width: 100%;
}
.input--title-error {
    color: $brand_danger;
    @include rem('font-size', 17px);
    line-height: ( 28px / 17px );
}

.textarea--excerpt {
    @include rem('padding', 21px 18px 46px);
    @include border-radius(0);
    @include box-shadow(none);
    resize: none;
    overflow: hidden;
}

.cke {
    border-color: $grey7 !important;

    .cke_top {
        border-bottom-color: $grey7 !important;
    }
    .cke_bottom {
        border-top-color: $grey7 !important;
    }

    .cke_contents {
    }

    &.cke_focus .cke_contents {
    }
}


.edit-article-authors {
    @include rem('font-size', 21px);
    line-height: ( 28px / 21px );
    @include SourceSansProSemibold();
    position: relative;
    
    &__input {
        display: block;
        @include rem('width', 300px);
        @include rem('margin', 7px 0);
        
        @media (min-width: $md_break) {
            display: inline-block;
            @include rem('width', 240px);
            @include rem('margin', 0 2px 0 4px);
        }
        @media (min-width: $lg_break) {
            @include rem('width', 226px);
        }
        @media (min-width: $xl_break) {
            @include rem('width', 300px);
        }
    }
    
    &__error {
        color: $brand_danger;
        @include rem('font-size', 17px);
        line-height: ( 28px / 17px );
        @include SourceSansProRegular();
        
        @media (min-width: $md_break) {
            position: absolute;
            @include rem('top', 48px);
            @include rem('left', 108px);
        }
    }
}

.excerpt_counter__area {
    position: relative;
}

.excerpt_counter {
    color: $grey7;
    @include rem('font-size', 17px);
    line-height: ( 24px / 17px );
    position: absolute;
    @include rem('right', 12px);
    @include rem('bottom', 7px);

    &.warn {
        color: $black;
    }

    &.super_warn {
        color: $brand_danger;
    }
}

