// Action line
// -------------------------------------------------------------
.action-line {
    position: relative;
    z-index: 1;
    @include clearfix();
    color: $white;
    background-color: $emperor;
    @include border-radius(3px);
    @include rem('padding-right', 3px);
    @include rem('padding-left', 20px);
    @include rem('min-height', 87px);
    @include rem('margin-bottom', 35px);
    
    @media (min-width: $md_break) {
        @include rem('margin-bottom', 56px);
    }

    a {
        color: $white;

        &.button {
            color: $link_color;
        }
    }

    // Left: status tekst - Kladd, Publisert,..
    &__status {
        color: $white;
        text-transform: uppercase;
        @include rem('font-size', 26px);
        line-height: ( 28px / 26px );
        @include rem('margin-top', 30px);
    }

    // Right: buttons, links,..
    .align_right {
        @include rem('padding-top', 22px);

        a,
        button,
        .button {
            @include rem('margin-right', 20px);
        }
    }
    @media (max-width: 540px) {
        .align_right {
            clear: left;
            float: left;
        }
    }

    // Link - f.eks: Avbryt
    &__link {
        display: inline-block;
        @include rem('font-size', 24px);
        line-height: ( 28px / 24px );
        @include rem('margin', 0 20px 14px 0);
    }

    // Forhåndsvisning
    &__preview-link {
        position: relative;
        display: inline-block;
        @include rem('font-size', 24px);
        line-height: ( 28px / 24px );
        @include rem('margin', 0 20px 14px 0);
        @include rem('padding-left', 41px);
        
        .preview_icon {
            position: absolute;
            left: 0;
            @include rem('top', -1px);
        }
        
        &--disabled {
            color: $silver;
            
            .preview_icon {
                background-position: -96px -128px;
            }
        }
    }

    // Remove margin-bottom
    label,
    select {
        margin-bottom: 0;
    }

    // Tilbakemelding
    &__feedback {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;

        &-message {
            position: absolute;
            top: 0;
            @include rem('right', 7px);
            @include rem('left', 7px);
            text-align: center;
            margin: 0 auto;

            div {
                display: block;
                color: $text_color;
                @include rem('font-size', 20px);
                line-height: ( 28px / 20px );
                @include rem('padding', 7px 14px 6px);
                background-color: $water;
                @include rem('border', 1px solid $spindle);
                border-top: 0 none;
                @include border-radius(0 0 3px 3px);
            }

            &--danger div {
                background-color: transparent;
                border-color: $brand_danger;
            }

            &--success div {
                background-color: $brand_success_bg;
                border-color: $brand_success;
            }
        }
    }
    @media (min-width: $md_break) {
        &__feedback-message {
            @include rem('width', 480px);
        }
    }
}