//
// Variables
// --------------------------------------------------


// Global values
// --------------------------------------------------

$main_sprite_url:       "../images/mainSprite.svg?v=" + 20171205 !default;
$service_sprite_url:    "../images/serviceSprite.svg?v=" + 20170201;
$image-path:            "../images" !default;


// Main colors
// -------------------------
$main_red:              #d7272d;

// Grays
// -------------------------
$black:                 #000;
$grey3:                 #333;
$emperor:               #535353;
$grey7:                 #727272;
$silver:				#a3a3a3;
$alto:					#e0e0e0;
$white_sand:			#f5f5f5;
$white:                 #fff;

// Secondary colors
// -------------------------
$bianca:                #fdfcf5; // rgba($rum_swizzle, 48/100)
$rum_swizzle:           #fbf7ed; // lys orange
$janna:                 #f5edd8; // lys orange 2
$almond:                #eee1c0; // menyfarge
$zombie:                #e7d5a6; // enda litt mørkere orange // border-color // #E8D7AB
$thunderbird:           #c01d1e; // mobil-meny border-color
$water:                 #f5f8fb; // lys blå
$spindle:               #bdcfe4; // border-color for lys blå
$danube:                #6f94c3; // buttons, viktig lenke
$danube_hover:          #4b79b4;  // darken($danube, 10%);
$nav_mobile_bg:         $main_red;

// Functional colors
// -------------------------
$brand_success:			#15ab29; // default-green // AA godkjent kun for stor tekst
$brand_success_bg:      #f7fdf8;
$brand_danger:			#cc0000; // default-red

// Font colors
// -------------------------
$text_color:            $grey3;
$text_selection:        #ffff00;

// Links
// -------------------------
$link_color:            #3b65a3;
$link_color_hover:      #2d4e7e; // darken($link_color, 10%);
$link_outline_color:    #f0c646;

// Forms
// -------------------------
$form_main_color:       $link_color;


// Typography
// -------------------------
$font_size_base:        16px !default;

$font_size_body:        ceil(($font_size_base * 1.11)) !default; // ~18px

$font_size_large:       ceil(($font_size_base * 1.25)) !default; // ~20px
$font_size_small:       ceil(($font_size_base * 0.85)) !default; // ~14px

$font_size_h1:          floor(($font_size_base * 1.85)) !default; // ~30px
$font_size_h2:          floor(($font_size_base * 1.5)) !default; // ~24px
$font_size_h3:          ceil(($font_size_base * 1.35)) !default; // ~22px
$font_size_h4:          ceil(($font_size_base * 1.35)) !default; // ~22px
$font_size_h5:          ceil(($font_size_base * 1.25)) !default; // ~20px
$font_size_h6:          ceil(($font_size_base * 1.25)) !default; // ~20px



// Component sizing
// -------------------------
$base_border_radius:                .1875rem;   // 3px
$border_radius_large:               .3125rem;   // 5px
$border_radius_larger:              .5rem;      // 8px



// Buttons
// -------------------------
$btnBackground:                     $grey7;
$btnBackgroundHighlight:            darken($btnBackground, 5%);

$btnPrimaryBackground:              $link_color !default;
$btnPrimaryBackgroundHighlight:     adjust-hue($btnPrimaryBackground, 20%) !default;

$btnInfoBackground:                 #5bc0de !default;
$btnInfoBackgroundHighlight:        #2f96b4 !default;

$btnSuccessBackground:              #62c462 !default;
$btnSuccessBackgroundHighlight:     #51a351 !default;

$btnWarningBackground:              lighten($brand_danger, 15%) !default;
$btnWarningBackgroundHighlight:     $brand_danger !default;

$btnDangerBackground:               #ee5f5b !default;
$btnDangerBackgroundHighlight:      #bd362f !default;




// Layout
// -------------------------
$site_max_width:                1170px;

$md_break:                      700px;
$lg_break:                      1020px;
$xl_break:                      1270px;

$md_break_max:                  $md_break - 1px;
$lg_break_max:                  $lg_break - 1px;
$xl_break_max:                  $xl_break - 1px;


$md_header_login_break:         860px;
$md_header_login_break_max:     $md_header_login_break - 1px;


