// Modal image position
// -------------------------------------------------------------
.modal__image-position {
    @include clearfix();

    &__legend {
        @include rem('margin-bottom', 7px);
    }

    &__button-container {
        position: relative;
        float: left;
        width: 50%;
    }

    label {
        position: relative;
        background-color: $white;
        color: $link_color;
        text-align: center;
        display: block !important;
        width: 100%;
        @include rem('padding', 3px 12px 4px 19px);
        @include rem('border', 1px solid $link_color);
        margin: 0;
        @include box-sizing();

        &:before {
            content: "";
            display: inline-block;
            position: relative;
            @include rem('top', 2px);
            @include rem('right', 8px);
            @include rem('width', 16px);
            @include rem('height', 16px);
            background-image: url($editModeSprite_url);
        }
    }

    input[type="radio"] {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
        filter: alpha(opacity=0);
        opacity: 0;
        margin: 0;
        outline: none;
        background: transparent;
        display: block;
        cursor: pointer;

        &:checked {
            cursor: inherit;
        }
    }

    // Border radius & icon
    .modal__image-position__label-center {
        @include border-radius(5px 0 0 5px);

        &:before {
            background-position: -8px -104px; // .align_center_icon
        }
    }
    .modal__image-position__label-right {
        @include border-radius(0 5px 5px 0);
        border-left: 0 none;

        &:before {
            background-position: -104px -72px; // .align_right_icon
        }
    }

    // Hover & active
    input[type="radio"]:hover + label,
    input[type="radio"]:active + label {
        background-color: $water;
    }

    // Checked
    input[type="radio"]:checked + label {
        background-color: $link_color;
        color: $white;
    }

    // Checked - white icons
    input[type="radio"].modal__image-position__input-center:checked + label.modal__image-position__label-center:before {
        background-position: -40px -104px;
    }
    input[type="radio"].modal__image-position__input-right:checked + label.modal__image-position__label-right:before {
        background-position: -136px -72px;
    }

    & input[type="radio"]:focus + label{
        @include outline();
        z-index: 6;
        pointer-events: none;
    }

    // Tablet & desktop
    @media (min-width: $md_break) {

        &__legend {
            float: left;
            width: 22%;
            margin: 0;
        }

        &__radiogroup {
            float: left;
            width: 78%;
        }
    }
}

