// Opplasting av bilder / med container div som begynner med følgende klasse:
.file-upload-input {
  position: relative;
  @include rem('border', 1px dashed $link_color);
  background-color: $white;
  @include rem('font-size', 20px);
  line-height: ( 24px / 20px );
  @include border-radius(3px);
  @include box-shadow(none);
  width: 100%;
  @include rem('min-height', 70px);
  text-align: center;
  vertical-align: baseline;
  cursor: pointer !important;
  display: inline-block !important;
  @include box-sizing();
  @include transition($property: background-color, $duration: .2s);
  opacity: 1;
  display: block;

  &:hover,
  &:active {
    background-color: $water;
  }
  // Hover for drag&drop
  &.hover {
    background-color: $water;
  }

  &__loader{
    background-color: rgba(255, 255, 255, 0.9);
    @include rem('height', 174px);
    position: absolute;
    display: block;
    top: 0;
    z-index: 99;
    width: 100%;
    height: 100%;
  }

  &:hover .file-upload-input__loader,
  &:active .file-upload-input__loader,
  &:focus .file-upload-input__loader,
  &.in .file-upload-input__loader {
    background-color: rgba(255, 255, 255, 0.9);
    @include transition($property: background-color, $duration: .2s);

  }

  &__image-preview {
    background-color: $white;
    position: relative;
    text-align: center;
    z-index: 1;

    img {
      background-color: $rum_swizzle;
      max-width: 100%;
      @include rem('margin', 7px);
      @include rem('min-height', 60px);
      @include rem('max-height', 160px);
    }
  }

  &:hover .file-upload-input__image-preview ,
  &:active .file-upload-input__image-preview,
  &:focus .file-upload-input__image-preview,
  &.in .file-upload-input__image-preview {
    background-color: $water;
  }

  // Upload an file-dialog
  &__filename-container {
    background-color: $white;
    position: relative;
    z-index: 1;


    .file-upload-input__filename {
      max-width: 100%;
      white-space: normal;
      text-align: center;
      @include rem('padding', 22px 12px);
      @include rem('min-height', 24px);
      @include rem('max-height', 160px);
    }
  }

  &__filename{
    color: $text_color !important;
  }

  &:hover .file-upload-input__image-preview,
  &:active .file-upload-input__image-preview,
  &:focus .file-upload-input__image-preview,
  &.in .file-upload-input__image-preview,
  &:hover .file_name_container,
  &:active .file_name_container,
  &:focus .file_name_container {
    background-color: $water;
    @include transition($property: background-color, $duration: .2s);
  }

  // Hover for drag&drop
  &.hover .file-upload-input__image-preview {
    background-color: $water;
  }

  .label {
    position: absolute;
    top: 32%;
    left: 0;
    width: 100%;
    text-align: center;
  }

  input[type="file"] {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    @include rem('font-size', 18px);
    line-height: ( 24px / 18px );
    text-align: left;
    filter: alpha(opacity=0);
    opacity: 0;
    outline: none;
    background: transparent;
    cursor: inherit;
    display: block;
  }
}



