// Modal window
// -------------------------------------------------------------
.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba($white, 72/100);
    z-index: 100;

    &__window {
        position: fixed;
        z-index: 1;
        background-color: $white_sand;
        @include rem('border', 1px solid $alto);
        width: 90%;
        @include rem('max-width', 640px);
        @include rem-box-shadow(0 1px 4px rgba($black, 16/100));
        @include border-radius();
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        overflow: hidden;


        &__title {
            color: $white;
            text-align: center;
            background-color: $link_color;
            @include rem('font-size', 24px);
            line-height: ( 28px / 24px );
            @include SourceSansProRegular();
            @include rem('padding', 14px);
            @include border-radius($base_border_radius $base_border_radius 0 0);
            margin: 0;

        }

        &__content {
            @include rem('padding', 14px 40px 28px);

            form {
                margin: 0;
            }

            label {
            }

            input {
                width: 100%;
            }

            button,
            .button {
                @include rem('margin', 0 7px);
            }

            .submit {
                color: $white;
                background-color: $link_color;
                border-color: $link_color;

                &:hover,
                &:active {
                    color: $white;
                    background-color: darken($link_color, 3%);
                    border-color: darken($link_color, 3%);
                }
            }
        }
    }

    &__cancel {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 0;
        cursor: default;
    }

    &--absolute{
        position: absolute;

        .modal__window{
            position: absolute;
            transform: translate(-50%, -60%);
        }
    }
}




// CKeditor dialog window
// -------------------------------------------------------------
.cke_dialog {
    .cke_dialog_body * {
        @include SourceSansProRegular();
        font-family: 'Source Sans Pro', Verdana, sans-serif;
        @include rem('font-size', 18px);
        line-height: ( 24px / 18px );

        label.cke_required{
            font-weight: 400;
        }
    }

    // Title
    .cke_dialog_title {
        color: $white;
        text-align: center;
        @include SourceSansProRegular();
        @include rem('font-size', 24px);
        line-height: ( 18px / 24px );
        text-shadow: none;
        background-color: $link_color !important;
        background-image: none !important;
        border-bottom: 0 none !important;
        @include rem('padding', 12px);
        @include box-shadow(none);
        @include rem-box-shadow(0 1px 4px rgba($black, 16/100));
        @include border-radius($base_border_radius $base_border_radius 0 0);
    }
    .cke_dialog_close_button {
        display: none !important;
    }

    // Content
    .cke_dialog_body {
        border: 0 none !important;
        background-color: $white_sand !important;
        @include border-radius($base_border_radius);
        @include rem-box-shadow(0 1px 4px rgba($black, 16/100));
    }
    .cke_dialog_contents {
        background-color: transparent;
    }
    .cke_dialog_contents_body {
        @include rem('padding', 18px 24px);
    }
    .cke_dialog_body label {
        display: inline-block;
        @include rem('padding-top', 5px);
    }

    input.cke_dialog_ui_input_text,
    input.cke_dialog_ui_input_password,
    textarea.cke_dialog_ui_input_textarea,
    select.cke_dialog_ui_input_select{
        background-image: -webkit-linear-gradient(hsla(0,0%,100%,0), hsla(0,0%,100%,0)); // Removing the inner shadow, rounded corners on iOS inputs
        @include rem('border', 1px solid $silver);
        color: $black;
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        @include rem('padding', 7px 10px 7px);
        @include border-radius(3px);
        @include rem-box-shadow(0 1px 2px $alto inset);
        margin: 0 !important;
        width: 100% !important;

        &:focus,
        &:focus,
        &:focus,
        &:focus {
            @include rem('border', 1px solid  $link_color);
            margin: 0 !important;
            @include rem('padding', 7px 10px 7px);
        }
    }

    textarea.cke_dialog_ui_input_textarea {
        white-space: normal;
    }

    // Footer
    .cke_dialog_footer {
        text-align: center;
        outline: none;
        @include box-shadow(none);
        background-image: none !important;
        background-color: transparent;
        border: none;

        .cke_dialog_footer_buttons {
            @include rem('margin', 0 0 18px);
        }

        a.cke_dialog_ui_button {
            @include rem('padding', 6px 12px 7px);
            box-shadow: none;
            text-decoration: none;
            border-radius: $base_border_radius;
            background-image: none;
            font-size: $font_size_large;
            font-size: 20px;
            @include rem('line-height', 1.4);
            cursor: pointer;


            &:hover {
                text-decoration: none;
            }

            span {
                text-shadow: none;
                @include rem('font-size', 20px);
                line-height: ( 24px / 20px );
                padding: 0;
                font-weight: 400;
            }
        }

        // Cancel
        a.cke_dialog_ui_button.cke_dialog_ui_button_cancel {
            color: $link_color;
            background-image: none;
            background-color: $white;
            border: 1px solid $link_color;
            @include rem('margin', 0 7px 0 12px);

            &:hover,
            &:focus,
            &:active {
                color: $white;
                background-color: $link_color;
            }
        }

        // Ok
        a.cke_dialog_ui_button.cke_dialog_ui_button_ok {
            @include rem('border', 1px solid $link_color);
            @include rem('margin', 0 12px);
            background-color: $link_color;
            color: $white;

            &:hover,
            &:focus,
            &:active {
                border-color: darken($link_color, 10);
                background-color: darken($link_color, 10);

            }
        }
    }
    .cke_resizer {
        display: none !important;
    }
}

.cke_dialog_ui_input_select {
    height: auto !important;
}

a.cke_dialog_tab {
    margin-right: 5px !important;
    @include rem('padding', 6px 12px 7px !important);
    background: none !important;
    background-image: none !important;
    background-color: $white !important;
    font-size: $font_size_large !important;
}

a.cke_dialog_tab_selected {
    @include rem('padding', 6px 12px 7px !important);
    background: none !important;
    background-image: none !important;
    background-color: $link_color !important;
    @include rem('border-bottom', 2px $link_color !important);
    color: $white !important;
    outline: none !important;

    & a:hover{
        background: none !important;
        background-color: $link_color !important;
        @include rem('border-bottom', 2px $link_color !important);
    }

    & a:focus{

    }
}

a.cke_dialog_tab:focus{
    @include rem('border', 1px solid #d1d1d1 !important);
    outline: none !important;
}

.cke_dialog_contents{
    @include rem('margin-top', 48px !important);
}
.cke_dialog_background_cover {
    background-color: rgba($white, 72/100) !important;
}
