//
// Spacing helpers
// --------------------------------------------------


// margin bottom
// --------------------------------

// margin bottom small
.mbs {
    @include rem('margin-bottom', 7px);
}
// margin bottom medium
.mbm {
    @include rem('margin-bottom', 14px);
}
// margin bottom medium large
.mbml {
    @include rem('margin-bottom', 21px);
}
// margin bottom large
.mbl {
    @include rem('margin-bottom', 28px); // default for <p>
}
// margin bottom extra large
.mbxl {
    @include rem('margin-bottom', 35px);
}
// margin bottom double extra large
.mbxxl {
    @include rem('margin-bottom', 42px);
}
// margin bottom jumbo
.mbj {
    @include rem('margin-bottom', 56px); // default between modules
}
// margin bottom none
.mbn {
    margin-bottom: 0;
}


@media (max-width: $md_break_max) {
    .mbs--mob {
        @include rem('margin-bottom', 7px);
    }
    .mbm--mob {
        @include rem('margin-bottom', 14px);
    }
    .mbml--mob {
        @include rem('margin-bottom', 21px);
    }
    .mbl--mob {
        @include rem('margin-bottom', 28px);
    }
    .mbxl--mob {
        @include rem('margin-bottom', 35px);
    }
    .mbxxl--mob {
        @include rem('margin-bottom', 42px);
    }
    .mbj--mob {
        @include rem('margin-bottom', 56px); // default between modules
    }
    .mbn--mob {
        margin-bottom: 0;
    }
}


@media (max-width: $lg_break_max) {
    .page_header_container_with_org_menu .page_header,
    .page_header_container_with_org_menu .h2_page_header_with_link {
        margin-bottom: 0;
    }
}


// Optical fix - if we have a readmore-link to the right, lift the next item
// --------------------------------
@media (min-width: $md_break) {
    .mbl .read_more_link_container {
        @include rem('margin-bottom', -28px); // 28 -> 0
    }
    .mbxl .read_more_link_container {
        @include rem('margin-bottom', -28px); // 35 -> 7
    }
    .mbxxl .read_more_link_container {
        @include rem('margin-bottom', -28px); // 42 -> 14
    }
    .mbj .read_more_link_container {
        @include rem('margin-bottom', -21px); // 56 -> 21
    }
}

